import React from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { CardBody, Heading, Skeleton, Text } from '@pancakeswap-libs/uikit'
import useI18n from 'hooks/useI18n'
import {
  useFarms,
  usePriceBnbBusd,
  useFarmCakePerBlock,
  usePriceCakeBusd,
} from "state/hooks";
import Card from 'components/custom/Card'
import { QuoteToken } from "config/constants/types";

const StyledTotalValueLockedCard = styled(Card)`
  align-items: center;
  display: flex;
  flex: 1;
`

const TotalValueLockedCard = () => {
  const TranslateString = useI18n()
  const farms = useFarms()
  const cakePrice = usePriceCakeBusd();
  const bnbPrice = usePriceBnbBusd();
  let tvl = new BigNumber(0)
  farms.forEach(farm => {
    let liquidity = farm.lpTotalInQuoteToken;
    if (!farm.lpTotalInQuoteToken) {
      liquidity = new BigNumber(0);
    }
    if (farm.quoteTokenSymbol === QuoteToken.BNB) {
      liquidity = bnbPrice.times(farm.lpTotalInQuoteToken);
    }
    if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
      liquidity = cakePrice.times(farm.lpTotalInQuoteToken);
    }
    tvl = tvl.plus(liquidity)
  })
  
  const totalValueFormated = tvl
    ? `${Number(tvl).toLocaleString(undefined, { maximumFractionDigits: 2 })}`
    : '-'

  return (
    <StyledTotalValueLockedCard>
      <CardBody>
        <Heading size="lg" mb="24px">
          {TranslateString(762, 'Total Value Locked (TVL)')}
        </Heading>
        {tvl ? (
          <>
            <Heading size="xl">{`$${totalValueFormated}`}</Heading>
            <br/>
            <Text color="textSubtle">{TranslateString(764, 'Across all internal and external LPs and Yield Farming Pools')}</Text>
          </>
        ) : (
          <>
            <Skeleton height={66} />
          </>
        )}
      </CardBody>
    </StyledTotalValueLockedCard>
  )
}

export default TotalValueLockedCard
